  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: 2px solid black;

  background: url(/lacanian-discourse-combinator/static/media/ouroboros.06840ad3.svg), url(/lacanian-discourse-combinator/static/media/radial.7665c133.svg);
  background-color: rgba(173, 167, 144, 0.7);
  background-size: contain, 43%;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: overlay, screen;
  /* backgound image'ı transparan yapılmıyor 
