}

.blaze-nodisplay {
  display: none;
}

.blaze-display  {
  display: grid;
  grid-template-rows: 1fr 0 1fr;
  grid-template-columns: 1fr;
  position: absolute;

  text-align: center;
  font-size: 20vh;
  color: chartreuse;
  text-shadow: 1vh 1vh 0 tomato;
  -webkit-text-decoration: wavy black;
          text-decoration: wavy black;
  z-index: 1600;
  

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
}

.blaze-display p:nth-of-type(1) {
  font-size: 35vh;
  color: tomato;
  text-shadow: 1vh 1vh 0 purple;
  
  -webkit-animation: swing 600ms infinite linear;
  
          animation: swing 600ms infinite linear;
}

.blaze-display p:nth-of-type(2) {

  -webkit-animation: pulse 150ms linear infinite alternate;

          animation: pulse 150ms linear infinite alternate;

}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.5) translateY(-20%);
            transform: scale(1.5) translateY(-20%);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(1.5) translateY(-20%);
            transform: scale(1.5) translateY(-20%);
  }
}

@-webkit-keyframes swing {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(-30deg)  translateX(10%);
            transform: rotate(-30deg)  translateX(10%);
  }
  75%  {
    -webkit-transform: rotate(30deg) translateX(-10%);
            transform: rotate(30deg) translateX(-10%);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes swing {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(-30deg)  translateX(10%);
            transform: rotate(-30deg)  translateX(10%);
  }
  75%  {
    -webkit-transform: rotate(30deg) translateX(-10%);
            transform: rotate(30deg) translateX(-10%);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
