.main-container {
  position: relative;
  height: 95vh + 2px;
  width: 95vw + 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid black;

  background: url(./assets/ouroboros.svg), url(./assets/radial.svg);
  background-color: rgba(173, 167, 144, 0.7);
  background-size: contain, 43%;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: overlay, screen;
  /* backgound image'ı transparan yapılmıyor 
  kendim editleyerek yaptım */
}

.blaze-nodisplay {
  display: none;
}

.blaze-display  {
  display: grid;
  grid-template-rows: 1fr 0 1fr;
  grid-template-columns: 1fr;
  position: absolute;

  text-align: center;
  font-size: 20vh;
  color: chartreuse;
  text-shadow: 1vh 1vh 0 tomato;
  text-decoration: wavy black;
  z-index: 1600;
  

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
}

.blaze-display p:nth-of-type(1) {
  font-size: 35vh;
  color: tomato;
  text-shadow: 1vh 1vh 0 purple;
  
  animation: swing 600ms infinite linear;
}

.blaze-display p:nth-of-type(2) {

  animation: pulse 150ms linear infinite alternate;

}

@keyframes pulse {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.5) translateY(-20%);
  }
}

@keyframes swing {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-30deg)  translateX(10%);
  }
  75%  {
    transform: rotate(30deg) translateX(-10%);
  }
  100% {
    transform: rotate(0deg);
  }
}