.discourse-name,
.matheme-cell {

    text-indent: 1%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    width: 100%;
    padding: 0 2px;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
}

.matheme-cell {
    text-align: center;
    background: rgba(240, 230, 140, 0.3);
}

.remove-button {
    padding: 0 2px;
    
    background: none;
    border: none;
    font-size: inherit;
    font-family: var(--mono-font);

    cursor: pointer;
}
