.outer-grid {
  display: grid;
  grid-template-rows: 18vh 2.5vh 18vh 18vh 18vh 2.5vh 18vh;
  grid-template-columns: auto 2.5vw 18vw 18vw 18vw 2.5vw auto;

  height: 100%;
  width: 100%;
  margin: 4px;
}

.image-container {
  justify-self: center;
  align-self: center;
}

button,
.discourse-container,
.image-container {
  /* makes all buttons, discourses, images unselectable */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
}

.clear-button {
  z-index: 1500;
  background: lightgrey;
  font-family: inherit;
  border: 1px solid;
  box-shadow: 2px 2px rgb(63, 63, 63);

  width: 60%;
  height: 15%;
  align-self: flex-start;
  margin-left: 1%;

  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 5;
  grid-column-end: 6;
}

.clear-button:hover {
  background: thistle;
  /* align-self: flex-end; */
}

.clear-button:active {
  background: greenyellow;
  transform: translateY(2px);
  box-shadow: 0px 0px rgb(63, 63, 63);
}

.nametable-open {
  width: 42%;
  height: 15%;
  align-self: flex-start;
  margin-left: 1%;
  margin-top: 0.8%;

  border: none;
  background: transparent;
  text-decoration: underline;
  cursor: pointer;

  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;

  z-index: 1500;
}

.nametable-open:hover {
  text-decoration-style: double;
  text-shadow: 0.8px 0.8px rgba(0, 0, 0, 0.1);

  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
}

/* bunlar başka türlü addresslenir mi? */
.custom-operation {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 4;
  grid-column-end: 5;
  width: min-content;
}
.custom-operation2 {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 7;
  grid-column-end: 8;
  width: min-content;
}
.custom-operation3 {
  grid-row-start: 7;
  grid-row-end: 8;
  grid-column-start: 4;
  grid-column-end: 5;
  width: min-content;
}
.custom-operation4 {
  grid-row-start: 4;
  grid-row-end: 5;
  grid-column-start: 1;
  grid-column-end: 2;
  width: min-content;
  justify-self: right;
}

.custom-operation > div,
.custom-operation2 > div,
.custom-operation3 > div,
.custom-operation4 > div {
  background: rgb(179, 168, 72);
}

.linarrow-image-container:nth-of-type(5) {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 4;
  grid-column-end: 5;
  height: 100%;
}

.discourse-grid {
  grid-column-start: 3;
  grid-column-end: 6;
  grid-row-start: 3;
  grid-row-end: 6;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;

  height: 100%;
  width: 100%;
}

.discourse-container {
  width: 18vw;
  height: 18vh;

  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;

  background: #9C433A;
  color: white;
  text-shadow: 1px 1px black;
  justify-self: center;
  border: 1px solid black;
  box-shadow: 1px 1px rgb(0, 0, 0, 0.4), 2px 2px rgb(0, 0, 0, 0.3),
    4px 4px rgba(0, 0, 0, 0.2);
}

.discourse-container:nth-of-type(2) {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
  background: #629C3A;
  justify-self: left;
}

.discourse-container:nth-of-type(3) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  background: #743A9C;
}

.discourse-container:nth-of-type(4) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  background: #3A939C;
  justify-self: right;
}

.discourse-container > div {
  height: 10%;

  display: flex;
  align-items: center;
  justify-content: center;

  border-bottom: 1px solid black;
}
.discourse-container .discourse-selector,
.discourse-container .discourse-display {
  height: 90%;
}

.stalin-image-container {
  height: 18vh;
  width: 18vh;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}

.stalin {
  border-radius: 50%;
  max-height: 100%;
  max-width: 100%;
}

.stalin:hover {
  cursor: crosshair;
  animation-name: blazeit;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  box-shadow: 0 0 10px #5eff00dc, 0 0 20px #308300dc, 0 0 30px #71ce08e1,
    0 0 40px #71ce08e1, 0 0 50px #71ce08e1, 0 0 60px #71ce08e1,
    0 0 70px #71ce08e1;
}

@keyframes blazeit {
  0% {
    transform: scale(1, 1) rotate(0deg);
  }
  12.5% {
    transform: scale(1.2, 1.2) rotate(45deg);
  }
  25% {
    transform: scale(1, 1) rotate(90deg);
  }
  37.5% {
    transform: scale(0.8, 0.8) rotate(135deg);
  }
  50% {
    transform: scale(1, 1) rotate(180deg);
  }
  62.5% {
    transform: scale(1.2, 1.2) rotate(225deg);
  }
  75% {
    transform: scale(1, 1) rotate(270deg);
  }
  87.5% {
    transform: scale(0.8, 0.8) rotate(315deg);
  }
  100% {
    transform: scale(1, 1) rotate(360deg);
  }
}

.cwarrowout-image-container,
.cwarrow-image-container {
  opacity: 0.7;
}

.cwarrow-image-container {
  height: 18vh;
  display: flex;
  justify-self: unset;
}

.cwarrow-image-container:nth-of-type(6) {
  padding-bottom: 8%;
  padding-right: 16%;
  padding-top: 16%;
  justify-self: right;
  align-self: flex-end;
}
.cwarrow-image-container:nth-of-type(7) {
  padding-bottom: 8%;
  padding-left: 16%;
  padding-top: 16%;
  justify-self: left;
  align-self: flex-end;
}

.cwarrow-image-container:nth-of-type(8) {
  padding-bottom: 16%;
  padding-right: 16%;
  padding-top: 8%;
  justify-self: right;
  align-self: flex-start;
}
.cwarrow-image-container:nth-of-type(9) {
  padding-bottom: 16%;
  padding-left: 16%;
  padding-top: 8%;
  justify-self: left;
  align-self: flex-start;
}
.cwarrow2,
.cwarrowout-2 {
  transform: rotate(90deg);
}
.cwarrow3,
.cwarrowout-3 {
  transform: rotate(180deg);
}
.cwarrow4,
.cwarrowout-4 {
  transform: rotate(270deg);
}

.cwarrowout-image-container {
  display: flex;
  height: 100%;
  width: 100%;

  padding-bottom: 5%;
  padding-top: 5%;
}

.cwarrowout-image-container:nth-of-type(6) {
  justify-content: flex-end;
  padding-right: 15%;

  grid-row-start: 1;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 4;
}

.cwarrowout-image-container:nth-of-type(7) {
  justify-content: flex-start;
  padding-left: 15%;

  grid-row-start: 1;
  grid-row-end: 4;
  grid-column-start: 5;
  grid-column-end: 8;
}

.cwarrowout-image-container:nth-of-type(8) {
  justify-content: flex-start;
  padding-left: 15%;

  grid-row-start: 5;
  grid-row-end: 8;
  grid-column-start: 5;
  grid-column-end: 8;
}

.cwarrowout-image-container:nth-of-type(9) {
  justify-content: flex-end;
  padding-right: 15%;

  grid-row-start: 5;
  grid-row-end: 8;
  grid-column-start: 1;
  grid-column-end: 4;
}
