.matheme-selector {
  font-size: 5vh;
  text-align-last:center;

}

.matheme-selector:nth-of-type(1) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  color: #9C433A;
}

.matheme-selector:nth-of-type(2) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  color: #629C3A;
}

.matheme-selector:nth-of-type(3) {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 4;
  color: #743A9C;
}

.matheme-selector:nth-of-type(4) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  color: #3A939C;
}
