.name-table {
    position: absolute;
    left: 8vw;
    top: 0.5vh;
    background: rgba(138, 125, 125, 0.95);
    border: solid black 1px;

    /* height ve width silinebilir de  */
    height: 36vh;
    width: 27vw;
  

    resize: both;
    overflow: auto;
    z-index: 1501;
    
  }

  .name-table-container {
      display: grid;
    grid-template-columns: 40fr 10fr 10fr 10fr 10fr auto;
    grid-template-rows: auto;

    
  }

  .name-table-container > span:nth-of-type(-n+6) {
    border-bottom: dashed 1px black;
    
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(255, 99, 71, 0.336);
  text-align: center;
  }

  
  .name-table-title {
    cursor: grab;
    display: flex;
    justify-content: space-between;
    background: #ababab;
    border-bottom: solid black 1px;
    width: 100%;
    padding: 0 0.4vw;
  
  }

  .name-table-title:active {
    cursor: grabbing;
  }
  
  .close {
    justify-self: right;
    cursor: pointer;
  
    /* makes [X] unselectable */
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
