.info-toggle {
    font-size: larger;
    color: black;
    position: absolute;
    right: 0;
    bottom: 0;

    font-family: var(--mono-font);

}

.info-toggle:visited {
color: black;
}

.info-toggle:hover {
animation: rainbow 2000ms infinite linear alternate;

}

@keyframes rainbow {
    0% {color:blueviolet}
    10% {color: plum;}
    20% {color: cornflowerblue;}
    30% {color: lightskyblue;}
    40% {color: lightseagreen;}
    50% {color: lime;}
    60% {color: yellowgreen;}
    70% {color: yellow;}
    80% {color: orange;}
    90% {color: tomato;}
    100% {color: rebeccapurple;}
    
}

.info-box {
    position: absolute;
    right: 5px;
    bottom: 30px;    

    max-height: 36%;
    width: 36%;

    border: solid 1px black;
    background: rgb(211, 211, 211);
    box-shadow: 2px 2px 5px black;

    overflow: auto;   

}

.info-text {
    padding-top: 1%;
    padding-right: 1%;
    padding-left: 4%;
    padding-bottom: 1%;
    margin: 1%;

    

    font-family: var(--mono-font);

    background: rgb(225, 225, 225);
    border: black solid 1px;


}

.info-text > * {
    padding: 1%;

}